import React from 'react';
import { View, StyleSheet, Dimensions } from 'react-native';
import HammerAnimation from './HammerAnimation';
import ReactNativeModal from 'react-native-modal';
import colors from '../../theme/colors';
import { AppLoadingOverlayProps } from '../../redux/features/app/types';
import { useSelector } from 'react-redux';
import { RootState } from '../../redux/store/types';

const width = Dimensions.get('window').width;

const LoadingOverlay = () => {
  const {
    size = width * 0.4,
    center = false,
    isVisible = false,
  }: AppLoadingOverlayProps = useSelector(
    (state: RootState) => state.app.loadingOverlay,
  );

  return (
    <ReactNativeModal
      animationIn="zoomIn"
      animationOut="zoomOut"
      isVisible={isVisible}
      useNativeDriver
      backdropOpacity={0.5}
      backdropColor={colors.white}
    >
      <View style={[styles.container, center && styles.containerCenter]}>
        <View style={[center && styles.logoContainerCenter]}>
          <HammerAnimation size={size} />
        </View>
      </View>
    </ReactNativeModal>
  );
};

const styles = StyleSheet.create({
  container: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    alignItems: 'center',
    justifyContent: 'center',
  },
  containerCenter: {
    justifyContent: 'center',
    alignItems: 'center',
  },
  logoContainerCenter: {
    position: 'relative',
    top: 'auto',
    flex: 1,
    justifyContent: 'center',
  },
});

export default LoadingOverlay;
